import React from 'react';
import ReactDOM from 'react-dom';
import {showAlert} from '../Alert';
import SubmitDataHelpers from '../helpers/SubmitDataHelpers.js';
import CookieHelpers from '../helpers/CookieHelpers';

import SingleEventStyle from '../../css/events/SingleEventStyle';
import '../../css/events/SingleEvent.css';

class SingleEvent extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            name: '',
            date: '',
            location: '',
            status: '',
        };

        this.updateState = this.updateState.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.addCourse = this.addCourse.bind(this);
        this.deleteCourse = this.deleteCourse.bind(this);
    }
    

    componentDidMount(){
        if(!window.sessionStorage.getItem("eventName")){
            window.location.href = '/events';
        }
        
        let eventName = window.sessionStorage.getItem("eventName");

        let helpers = new SubmitDataHelpers;

        helpers.postData('/events/singleEvent',
        {
            eventName: eventName,
        }).then(res => {
            if(!res.error && window.location.pathname === ('/events/' + encodeURI(eventName))){
                //console.log(res);
                let date = new Date();
                let today = date.getFullYear() + '-' + (date.getMonth()<9? '0' + (date.getMonth()+1): date.getMonth() + 1) + '-' + date.getDate();
                //console.log(res[0].date + ' ' + today);
                if(res[0].date > today){
                    res[0].status = 'Coming';
                } else if(res[0].date == today){
                    res[0].status = 'Opening';
                } else {
                    res[0].status = 'Closed';
                }
                //console.log(res[0].status);
                this.setState(res[0]);
                this.renderCourses(res[0]);
                this.showAddCourse(res[0].status);
            } else {
                //console.log(res);
                showAlert(res.error, 5000);

                this.setState({error: res.error});
                window.location.href = '/events';
            }
        });

        SingleEventStyle();
    }

    componentDidUpdate(){
        SingleEventStyle();
    }

    render(){
        let form = [];
        let classNameInput = 'seinput col-5 border-bottom pt-2 pb-1 mb-2';

        let classNameSave, classNameDelete;
        classNameSave = classNameDelete = 'sebtn';
        classNameSave += ' save';
        classNameDelete += ' delete';

        if(this.state.status == 'Coming'){
            form.push(
                <form key="changeEvent" className=" mx-auto">
                    <div id="formInput" className="formInput col-12 col-sm-11 mx-auto py-1">
                        <div className="inputWrap col-12 p-0 d-flex flex-row my-3">
                            <input className={classNameInput} type="text" name="name" value={this.state.name} disabled />
                            <div className=" col-2 d-flex justify-content-center align-items-center"><i id="saveChange" className="fas fa-save" onClick={this.handleSubmit}></i></div>
                            <input className={classNameInput} type="date" name="date" value={this.state.date} onChange={this.updateState} placeholder="Event Date" required />
                        </div>
                        <div className="inputWrap col-12 p-0 d-flex flex-row my-3">
                            <input className={classNameInput} type="text" name="location" value={this.state.location} onChange={this.updateState} placeholder="Location" required />
                            <div className=" col-2 d-flex justify-content-center align-items-center"><i id="deleteEvent" className="fas fa-trash-alt" onClick={this.handleSubmit}></i></div>
                            <input className={classNameInput} type="text" name="status" value={this.state.status} onChange={this.updateState} placeholder="Status" disabled/>
                        </div>
                    </div>
                </form>
            );
        } else {
            form.push(
                <form key="changeEvent" className=" mx-auto">
                    <div id="formInput" className="formInput col-12 col-sm-11 mx-auto py-1">
                        <div className="inputWrap col-12 p-0 d-flex flex-row my-3">
                            <input className={classNameInput} type="text" name="name" value={this.state.name} disabled />
                            <div className="col-2"></div>
                            <input className={classNameInput} type="date" name="date" value={this.state.date} onChange={this.updateState} placeholder="Event Date" disabled />
                        </div>
                        <div className="inputWrap col-12 p-0 d-flex flex-row my-3">
                            <input className={classNameInput} type="text" name="location" value={this.state.location} onChange={this.updateState} placeholder="Location" disabled />
                            <div className="col-2"><i id="deleteEvent" className="closedEventDeleteBtn fas fa-trash-alt" onClick={this.handleSubmit}></i></div>
                            <input className={classNameInput} type="text" name="status" value={this.state.status} onChange={this.updateState} placeholder="Status" disabled/>
                        </div>
                    </div>
                </form>
            );
        }

        return(
            <div id="singleEvent" className="singleEvent">
                <div className='title pb-3'>
                    <h2>{this.state.name}</h2>
                </div>

                <div className="formInputWrap col p-0">
                    {form}
                </div>

                <div className="courseTable mx-auto mt-4 shadow px-2 pt-2 pb-0 border">
                    <div className="tcaption text-left pl-1 pt-1 pb-2">Courses ( {this.state.courseList? this.state.courseList.length : '0'} courses )</div>
                    <div className="table mx-auto border">
                        <div className="thead col-12">
                            <div className="tr row py-3">
                                <div className="th col-3">Type</div>
                                <div className="th col-3">Start</div>
                                <div className="th col-3">End</div>
                                <div className="th col-3">Status</div>
                            </div>
                        </div>
                        <div className="tbody col-12 overflow-auto">
                            {this.state.courseList? this.state.courseList: 'No courses'}
                        </div>
                    </div>

                    {this.state.addCourseButton}
                </div>
            </div>
        );
    }

    updateState(e){
        let state = {...this.state};

        switch(e.target.name){
            case 'name':
                state.name = e.target.value;
                this.setState(state);
                break;
            case 'date':
                let date = new Date();
                let today = date.toISOString().substring(0,10);

                //if(e.target.value > today){
                    state.date = e.target.value;
                    this.setState(state);
                //} else {
                    //alert("Date cannot be in the past");
                   // showAlert("Date cannot be in the past", 5000);
                //}

                break;
            case 'location':
                state.location = e.target.value;
                this.setState(state);
                break;
            default:
                break;
        }
    }

    handleSubmit(e){
        e.preventDefault();
        let error = '';

        let date = new Date();
        let today = date.toISOString().substring(0,10);
        let timezone = date.getTimezoneOffset()/(-60) + ":00:00";

        let helpers = new SubmitDataHelpers;

        if(e.target.id === "saveChange"){
            if(this.state.date <= today){
                error = 'Event date cannot be in the past.'
            }

            if(error === ''){
                //let creator = window.sessionStorage.getItem("username");
                let cookieHelpers = new CookieHelpers;
                let creator = cookieHelpers.getCookie("username");

                helpers.postData('/events/editevent/', 
                    {
                        name: this.state.name.toLowerCase(),
                        date: this.state.date,
                        timezone: timezone,
                        location: this.state.location,
                        status: this.state.status,
                        creator: creator,
                    }
                ).then((res) => {
                    console.log(res); // JSON data parsed by `response.json()` call
                    if(res.error){
                        //console.log(res.error);
                        showAlert(res.error, 5000);

                        let state = {...this.state};
                        state.error = res.error;
                        this.setState(state);
                    } else {
                       window.location.reload();
                    }
                });
            } else {
                showAlert(error, 5000);

                let state = {...this.state};
                state.error = error;
                this.setState(state);
            }
        } else if(e.target.id === "deleteEvent"){
            if(window.confirm("Delete this course...")){
                helpers.postData('/events/deleteevent/', 
                    {
                        name: this.state.name.toLowerCase(),
                    }
                ).then((res) => {
                    //console.log(res); // JSON data parsed by `response.json()` call
                    if(res.error){
                        //console.log(res.error);
                        showAlert(res.error, 5000);

                        let state = {...this.state};
                        state.error = res.error;
                        this.setState(state);
                    } else {
                        window.location.href = "/events";
                    }
                });
            }
        }
    }

    showAddCourse(eventStatus){
        if(eventStatus == 'Coming'){
            let addCourseButton = [];

            addCourseButton.push(
                <div key="addCourse" className=" addCourse mt-2 mb-0 col-12" data-toggle="tooltip" data-placement="top" title="Add course...">
                    <button className=" addCourseBtn rounded-circle p-3 border shadow" onClick={this.addCourse}>
                        <i className="fas fa-plus"></i>
                    </button>
                </div>
            );
            this.setState({...this.state, ['addCourseButton']: addCourseButton});
        }
    }

    renderCourses(event){
        if(!event.courses.error){
            let date = new Date();
            let currentTime = date.toLocaleTimeString().substring(0,8);
            let courses = event.courses;

            courses.forEach(course => {
                if(event.status == "Coming"){
                    course.status = 'Coming';
                } else if(event.status == "Opening"){
                    if(course.startTime > currentTime){
                        course.status = 'Coming';
                    } else if(course.startTime < currentTime && course.endTime > currentTime){
                        course.status = 'Opening';
                    } else{
                        course.status = "Closed";
                    }
                } else {
                    course.status = "Closed";
                }
            });

            let content = [];

            courses.forEach(course =>{
                content.push(
                    <div className="course tr row py-2" 
                        key={course.type} value={course.type}
                        onClick={() => this.selectCourse(course.type, course.password)} 
                        onContextMenu={e => this.deleteCourse(e, course.type)}
                    >
                        <div className="td col-3">{course.type}</div>
                        <div className="td col-3">{course.startTime}</div>
                        <div className="td col-3">{course.endTime}</div>
                        <div className="td col-3">
                            {course.status}
                            <i className="deleteCourse fas fa-minus-circle" 
                                data-toggle="tooltip" data-placement="top" title="Delete event..."
                                onMouseDown={e => this.deleteCourse(e, course.type)}
                            ></i>
                        </div>
                    </div>
                );
            });

            this.setState({...this.state, ['courseList']: content});
        } else {
            //showAlert(event.courses.error, 5000);

            this.setState({...this.state, ['error']: event.courses.error});
        }
    }

    selectCourse(courseType, courseAccessKey){
        if(!this.state.deletingCourse){
            window.sessionStorage.setItem("courseType", courseType);
            window.sessionStorage.setItem("view", "creatorView");
            window.sessionStorage.setItem("eventDate", this.state.date);
            window.sessionStorage.setItem("courseAccessKey", courseAccessKey)

            window.location.href += '/' + courseType + '/' + courseAccessKey;
        } else {
            this.setState({deletingCourse: false});
        }
    }

    async addCourse(){
        if(this.state.courseList && this.state.courseList.length > 2){
            showAlert('All courses have been added, cannot add more courses...', 5000);
        } else {
            let module = await import(`../courses/CreateCourse`);
            const CreateCourse = await module.default;

            let modal = document.createElement("div");
            modal.id = "popUpModal";
            modal.className = "popUpModal";

            if(window.localStorage.getItem('darkThemeMode') == 'on'){
                modal.className += " dark-popUpModal";   
            }

            document.body.appendChild(modal);
            ReactDOM.render(<CreateCourse />, document.getElementById('popUpModal'));
        }
    }

    deleteCourse(e, courseType){
        this.setState({deletingCourse: true});

        e.preventDefault();

        if(window.confirm("Delete this course...")){
            let error = '';

            //let date = new Date();
            //let today = date.toISOString().substring(0,10);

            let helpers = new SubmitDataHelpers;
            helpers.postData('/courses/deletecourse/', 
            {
                event: this.state.name.toLowerCase(),
                type: courseType,
            }).then((res) => {
                console.log(res); // JSON data parsed by `response.json()` call
                if(res.error){
                    //console.log(res.error);
                    showAlert(res.error, 5000);
                    
                    let state = {...this.state};
                    state.error = res.error;
                    this.setState(state);
                } else {
                    window.location.reload();
                }
            });
        }
    }
}

export default SingleEvent;