import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function SingleEventStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default SingleEventStyle;

function setDarkLightMode(){
    darkLightMode('singleEvent');
}

function setStyle(){
    setInputWrap(); 

    if(eClass('addCourse')[0]){
        eId('singleEvent').style.height = eId('nav').offsetTop + 'px';
        eClass('courseTable')[0].style.height = (eId('singleEvent').offsetHeight - eClass('courseTable')[0].offsetTop) * 0.98 + 'px';
        eClass('table')[0].style.height = (eId('nav').offsetTop - eClass('table')[0].offsetTop - eClass('addCourseBtn')[0].offsetHeight) * 0.9 + 'px';
        eClass('tbody')[0].style.height = eClass('table')[0].offsetHeight -  eClass('addCourseBtn')[0].offsetHeight + 'px';
    } else {
        eId('singleEvent').style.height = eId('nav').offsetTop + 'px';
        eClass('courseTable')[0].style.height = (eId('singleEvent').offsetHeight - eClass('courseTable')[0].offsetTop) * 0.98 + 'px';
        eClass('table')[0].style.height = (eId('nav').offsetTop - eClass('table')[0].offsetTop) * 0.7 + 'px';
        eClass('tbody')[0].style.height = eClass('table')[0].offsetHeight -  eClass('thead')[0].offsetHeight + 'px';
    }

    //setTHead();
    setAddCourse();
}

function setInputWrap(){
    for(let i = 0; i < eClass('inputWrap').length; i++){
        let inputWrap = eClass('inputWrap')[i];

        if(window.innerWidth <= 576){
            inputWrap.className = inputWrap.className.replace(' my-3', '');
        } else {
            inputWrap.className = inputWrap.className.replace(' my-3', '') + ' my-3';
        }
    }
}

function setTHead(){
    let theadRow = eClass('thead')[0].getElementsByClassName('row')[0];

    if(window.innerWidth < 576){
        theadRow.className = theadRow.className.replace(' py-3', ' py-2');
    } else {
        theadRow.className = theadRow.className.replace(' py-2', ' py-3');
    }
}

function setAddCourse(){
    if(eClass('addCourse')[0]){
        eClass('addCourse')[0].style.height = eClass('addCourseBtn')[0].offsetHeight + 'px';
        eClass('addCourseBtn')[0].style.width = eClass('addCourseBtn')[0].offsetHeight + 'px';
        if(eClass('course').length > 2){
            eClass('fa-plus')[0].style.color = 'rgba(150,150,150,0.8)';
        }
    }
}
